"use client";
import { getProfileInfo } from "@/app/_service/UserService";
import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { User, ShoppingCart, Heart, ClipboardList, MapPin } from "lucide-react"; // Import icons from lucide-react
import Wishlist from "../../wishlist/page";
import OrderPage from "../../orders/page";
import Cart from "../../cart/Cart";

const UserProfile = () => {
  const { user, loading, error, token } = useSelector((state) => state.userData);
  const userId = user?.id;
  const [activeSection, setActiveSection] = useState("profile"); // State to track active section
  const [profileData, setProfileData] = useState(null);
  
  useEffect(() => {
    const fetchProfileInfo = async () => {
      try {
        const response = await getProfileInfo(token, userId);
        setProfileData(response.data.data);
      } catch (err) {
        console.error(err);
      }
    };

    if (userId && token) {
      fetchProfileInfo();
    }
  }, [userId, token]);

  if (loading) {
    return <div className="text-center">Loading...</div>;
  }

  if (error) {
    return <div className="text-center text-red-500">{error}</div>;
  }

  return (
    <div className="flex">
      {/* Sidebar Navigation */}
      <nav className="w-64 bg-gray-800 text-white h-screen p-4">
        <div className="flex items-center mb-6">
          <div className="w-12 h-12 rounded-full overflow-hidden bg-gray-700 flex items-center justify-center">
            {user.profile_pic ? (
              <img src={user.profile_pic} alt="Profile" className="w-full h-full object-cover" />
            ) : (
              <span className="text-2xl font-bold text-gray-300">{user.name?.charAt(0)}</span>
            )}
          </div>
          <h2 className="ml-2 text-lg font-semibold">{user.name}</h2>
        </div>
        <ul className="space-y-2">
          <li>
            <button onClick={() => setActiveSection("profile")} className="flex items-center p-2 rounded hover:bg-gray-700">
              <User  className="w-5 h-5 mr-2" /> Profile
            </button>
          </li>
          <li>
            <button onClick={() => setActiveSection("wishlist")} className="flex items-center p-2 rounded hover:bg-gray-700">
              <Heart className="w-5 h-5 mr-2" /> Wishlist
            </button>
          </li>
          <li>
            <button onClick={() => setActiveSection("orders")} className="flex items-center p-2 rounded hover:bg-gray-700">
              <ClipboardList className="w-5 h-5 mr-2" /> Orders
            </button>
          </li>
          <li>
            <button onClick={() => setActiveSection("editProfile")} className="flex items-center p-2 rounded hover:bg-gray-700">
              <User  className="w-5 h-5 mr-2" /> Edit Profile
            </button>
          </li>
          <li>
            <button onClick={() => setActiveSection("editAddress")} className="flex items-center p-2 rounded hover:bg-gray-700">
              <MapPin className="w-5 h-5 mr-2" /> Edit Address
            </button>
          </li>
          <li>
            <button onClick={() => setActiveSection("cart")} className="flex items-center p-2 rounded hover:bg-gray-700">
              <ShoppingCart className="w-5 h-5 mr-2" /> Cart
            </button>
          </li>
        </ul>
      </nav>

      {/* Main Content */}
      <div className="flex-1 p-6 bg-gray-100">
        <div className="max-w-4xl mx-auto p-6 bg-white rounded-lg shadow-md">
          {activeSection === "profile" && (
            <div>
              <h2 className="text-2xl font-semibold text-gray-800">{user.name}</h2>
              <p className="text-gray-600">{user.email}</p>
              <p className="text-gray-600">{user.phone}</p>
            </div>
          )}
          {activeSection === "profile" && (
            <div>
              <h3 className="text-lg font-semibold text-gray-800">Edit Profile</h3>
              <p className="text-gray-600">Update your information</p>

            </div>
          )}
          {activeSection === "editAddress" && (
            <div>
              <h3 className="text-lg font-semibold text-gray-800">Edit Address</h3>
              <p className="text-gray-600">Update your addresses</p>
              <Link href="/user/profile/address" className="mt-2 text-blue-500 hover:underline">Edit Address</Link>
            </div>
          )}
          {activeSection === "wishlist" && (
            <div>
              <h3 className="text-lg font-semibold text-gray-800">Your Wishlist</h3>
              <p className="text-gray-600">Items you have saved for later.</p>
<Wishlist/>            </div>
          )}
          {activeSection === "orders" && (
            <div>
              <h3 className="text-lg font-semibold text-gray-800">Your Orders</h3>
              <p className="text-gray-600">View your past orders.</p>
<OrderPage/>            </div>
          )}
          {activeSection === "cart" && (
            <div>
              <h3 className="text-lg font-semibold text-gray-800">Your Cart</h3>
              <p className="text-gray-600">Items currently in your cart.</p>
<Cart/>            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default UserProfile;